/*
rgb(1, 183, 207)  #01b7cf
yellow | rgb(234,232,70)
 */
main {
  display: grid;
  gap: 1rem; }

@media (max-width: 991.98px) {
  main {
    grid-template-columns: 1fr; }
  #mycard--experience {
    grid-column: 1;
    grid-row: 2; }
  #mycard--contact {
    grid-column: 1;
    grid-row: 1; }
  #mycard--career {
    grid-column: 1;
    grid-row: 3; }
  #mycard--edu {
    grid-column: 1;
    grid-row: 4; }
  #mycard--skills {
    grid-column: 1;
    grid-row: 5; } }

@media (min-width: 991.98px) {
  main {
    grid-template-columns: 2fr 1fr; }
  #mycard--experience {
    grid-column: 1;
    grid-row: 1 / 5; }
  #mycard--contact {
    grid-column: 2;
    grid-row: 1; }
  #mycard--career {
    grid-column: 2;
    grid-row: 2; }
  #mycard--edu {
    grid-column: 2;
    grid-row: 3; }
  #mycard--skills {
    grid-column: 2;
    grid-row: 4; } }

@media print {
  main {
    grid-template-columns: 1fr 1fr; }
  #mycard--experience {
    grid-column: 1 / 3;
    grid-row: 2; }
  #mycard--contact {
    grid-column: 1;
    grid-row: 1; }
  #mycard--career {
    grid-column: 2;
    grid-row: 1; }
  #mycard--edu {
    grid-column: 1;
    grid-row: 3; }
  #mycard--skills {
    grid-column: 2;
    grid-row: 3; } }

.App {
  padding: 0 1rem;
  padding-block-end: 1rem;
  min-height: 100vh; }

.App--light {
  background-color: inherit;
  color: inherit; }
  .App--light a {
    color: inherit; }

.App--dark {
  background-color: #262626;
  color: #f9fafc; }
  .App--dark a {
    color: #f9fafc; }

@media print {
  .App--dark {
    background-color: #f9fafc;
    color: #262626; }
    .App--dark a {
      color: #262626; } }
