.skills {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: .2rem;
  grid-column-gap: .5rem;
  justify-self: stretch;
  margin: 0 1rem; }
  .skills label {
    grid-column: 1;
    text-align: right; }
  .skills .score-bar {
    grid-column: 2;
    background-color: #f9fafc;
    height: 1rem; }
    .skills .score-bar .score-bar__score {
      background-color: #b7c2ce;
      height: 1rem; }
      .skills .score-bar .score-bar__score span {
        display: none; }
      @media print {
        .skills .score-bar .score-bar__score span {
          display: inherit; } }
