h1 {
  margin: 1rem 0;
  font-weight: 700; }

header {
  display: flex;
  flex-flow: row wrap; }

header div {
  flex: 1 auto; }

header > div:last-child > ul {
  list-style: none;
  padding: 0;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around; }
  @media (min-width: 767.98px) {
    header > div:last-child > ul {
      justify-content: flex-end; } }
  header > div:last-child > ul li:first-child {
    margin-left: 0; }
  header > div:last-child > ul li {
    margin-left: 1rem; }

@media print {
  header div:last-child {
    display: none; } }
