/*
rgb(1, 183, 207)  #01b7cf
yellow | rgb(234,232,70)
 */
.mycard {
  background: linear-gradient(#ebeef3, #f9fafc); }

.App--dark .mycard {
  background: #262626; }

.App--dark .mycard__title {
  color: #262626; }

.mycard__title {
  background-color: #b7c2ce;
  text-align: center;
  padding-block-start: .1em;
  padding-block-end: .1em;
  min-height: 1rem; }

.mycard__title h2 {
  margin-block-start: 0;
  margin-block-end: 0; }

.mycard__body {
  padding: .8rem 2%; }
